import React from 'react'
import axios from 'axios'
import ReactHTMLTableToExcel from 'react-html-table-to-excel';

class App extends React.Component {

  state = {
    items: [""],
    play: 0,
    result: []
  }

  componentDidMount() {
    /*
    
    */
  }

  onChange = (ind) => (e) => {
    let items = [...this.state.items]
    items[ind] = e.target.value
    this.setState({ items })
  }

  onAddPole = (e) => {
    let items = [...this.state.items]
    items.push(undefined)
    this.setState({ items })
  }
  onAddPoleX= (e) => {
    let items = [...this.state.items]
    for(let i = 0; i < 10; i++)
      items.push(undefined)
    this.setState({ items })
  }

  onPlay = () => {
    this.setState({ result: [] })
    let items = this.state.items
    items.map((i, ind) => {
      let otv = i ? this.Fetch(i, ind) : '';
    })
  }

  Fetch = (url, ind) => {
    let th = this
    axios.get(url, {
      crossDomain: true,
      method: 'GET',
  mode: 'no-cors',
  headers: {
    'Access-Control-Allow-Origin': '*',
    'Content-Type': 'application/json',
  },
  withCredentials: true,
  credentials: 'same-origin',
    }).then(function (response) {
        let item = {}
        let el = document.createElement("div");
        el.innerHTML = response.data;
        let title = el.getElementsByClassName("heading-size-1")
        item.title = title[0].outerText
        let img = el.getElementsByTagName("ins")[0].outerHTML;
        let imgreg = /htt(.*?)\);/g;
        let imgres = img.match(imgreg)[0];
        imgres = imgres.replace(")", "");
        imgres = imgres.replace(";", "");
        item.img = imgres;
        let leftdata = el.getElementsByTagName("td");
        let leftdata1 = leftdata[0].outerText.match(/\[money=(.*?)\]/g)[1];
        leftdata1 = leftdata1.replace("[money=", "");
        leftdata1 = leftdata1.replace("]", "");
        if (!item['poch']) item['poch'] = {}
        item.poch = {
          g: Math.floor(+leftdata1 / 1000),
          s: Math.floor((+leftdata1 % 1000) / 100),
          b: +leftdata1 % 100
        }
        let flag = undefined
        
        let centrdata9999 = el.getElementsByTagName("noscript")
  
        for (let i = 0; i < centrdata9999.length; i++) {
          let temp_text = centrdata9999[i].outerText
          let temp0 = temp_text.match(/Уровень предмета:/g)
          if (temp0 && temp0.length > 0) flag = i
        }
  
        let centrdata = el.getElementsByTagName("noscript")[flag].outerText;
        let centrdata1 = centrdata.match(/<td>(.*?)<\/td>/g);
        let centrdata11 = centrdata1[0].replace(/<(.*?)>/g, ";");
        centrdata11 = centrdata11.replace("Уровень предмета: ;", "")
        centrdata11 = centrdata11.split(";")
        centrdata11 = centrdata11.map(i => { if (i) return i; });
        centrdata11 = centrdata11.filter(i => i)
        item.lvlpr = centrdata11[0];
        item.subtitle = centrdata11[1];
        item.where = centrdata11[2];
        let centrdata2 = centrdata1[1].replace(/<(.*?)>/g, ";");
        
        let type = centrdata.match(/class=\"q1\">(.*?)<\/span/g)[0];
        type = type.replace("class=\"q1\">", "")
        type = type.replace("<\/span", "")
        item.type = type
        let arm = centrdata.match(/--amr-->Броня\: (.*?)<\/span/g)[0];
        arm = arm.replace("--amr-->Броня: ", "")
        arm = arm.replace("<\/span", "")
        item.arm = +arm
  
        let stat = centrdata.match(/stat(\d){1,4}-->(.*?)<\/spa/g);
        let stats = []
        stat.map((i, ind) => {
          let stat0 = stat[ind].replace("</spa", "")
          stat0 = stat0.split(">")[1]
          stats.push(stat0)
        })
        let stat1 = centrdata.match(/rtg(\d){1,4}-->(.*?)<\/spa/g);
        if (stat1 && stat1.length > 0) {
          stat1.map((i, ind) => {
            let stat0 = stat1[ind].replace("</spa", "")
            stat0 = stat0.split(">")[1]
            stats.push(stat0)
          })
        }
        item.stat = stats
  
        let cenag = centrdata.match(/class\=\"moneygold\">(.*?)<\/spa/g);
        let cenac = centrdata.match(/class\=\"moneysilver\">(.*?)<\/spa/g);
        let cenab = centrdata.match(/class\=\"moneycopper\">(.*?)<\/spa/g);
  
        if (!item.price) item.price = {}
  
        if (cenag && cenag.length !== 0) {
          let tcenag = cenag[0].replace("class=\"moneygold\">", "");
          tcenag = tcenag.replace("</spa", "");
          item.price['g'] = +tcenag
        }
  
        if (cenac && cenac.length !== 0) {
          let tcenac = cenac[0].replace("class=\"moneysilver\">", "");
          tcenac = tcenac.replace("</spa", "");
          item.price['s'] = +tcenac
        }
  
        if (cenab && cenab.length !== 0) {
          let tcenab = cenab[0].replace("class=\"moneycopper\">", "");
          tcenab = tcenab.replace("</spa", "");
          item.price['b'] = +tcenab
        }
  
        let prochn = centrdata.match(/Прочность: (\d){1,9} \/ (\d){1,9}<\/td/g);
        prochn = prochn[0].replace("Прочность: ", "")
        prochn = prochn.replace("</td", "")
        prochn = prochn.split('/');
        item.prochnost = +prochn[0]
        
        let forid = el.getElementsByTagName("script")
        let flag2 = undefined
  
        for (let i = 0; i < forid.length; i ++) {
          let temp_text = el.getElementsByTagName("script")[i].outerText
          let temp0 = temp_text.match(/name_ruru/g)
          if (temp0 && temp0.length > 0) flag2 = i
        }
  
        if (flag2 !== undefined) {
          forid = el.getElementsByTagName("script")[flag2].outerText
          let id = forid.match(/icon\":\"(.*?)\",\"/g)[0];
          id = id.replace(":", ",").split(",")[1].replace("\"","").replace("\"","")
          item.id = id
        }
        item.url = url

        item.bigImg = `https://wow.zamimg.com/images/wow/icons/large/${item.id}.jpg`
        console.log(item)

        let res = th.state.result
        res[ind] = {...item, error: false }
        th.setState({ result: res })
      })
      .catch(err => {
        let res = th.state.result
        res[ind] = { error: true }
        th.setState({ result: res })
      })
  }

  render () {
    return (
      <div>
        {this.state.items.map((i, ind) => (
          <>
            <input placeholder="URL" value={this.state.items[ind]} onChange={this.onChange(ind)} />
            <br />
          </>
        ))}
        <br />
        <input type="button" value="добавить поле" onClick={this.onAddPole}/>
        <input type="button" value="добавить поле x10" onClick={this.onAddPoleX}/>
        <br />
        <input type="button" value="Начать" onClick={this.onPlay}/>
        {this.state.result.length > 0 && <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="download-table-xls-button"
          table="table-to-xls"
          filename="tablexls"
          sheet="tablexls"
          buttonText="Download as XLS"
        />}
        <table id="table-to-xls">
          <tr>
            <th>обработано</th>
            <th>id</th>
            <th>название</th>
            <th>тип</th>
            <th>слот</th>
            <th>добавляет брони</th>
            <th>картинка</th>
            <th>большая картинка</th>
            <th>ур. предмета</th>
            <th>починка</th>
            <th>цена</th>
            <th>прочности</th>
            <th>статистика</th>
            <th>доп. свед</th>
            <th>url</th>
          </tr>
          {this.state.result.map((i,ind) => (
            <tr>
              <td>{i.error ? 'Нет' : 'Да'}</td>
              <td>{i.id}</td>
              <td>{i.title}</td>
              <td>{i.type}</td>
              <td>{i.where}</td>
              <td>{i.arm}</td>
              <td>{i.img}</td>
              <td>{i.bigImg}</td>
              <td>{i.lvlpr}</td>
              <td>{`зол: ${i.poch.g ? i.poch.g : '0'}, сер: ${i.poch.s ? i.poch.s : '0'}, бр: ${i.poch.b ? i.poch.b : '0'}`}</td>
              <td>{`зол: ${i.price.g ? i.price.g : '0'}, сер: ${i.price.с ? i.price.с : '0'}, бр: ${i.price.b ? i.price.b : '0'}`}</td>
              <td>{i.prochnost}</td>
              <td>{i.stat + ""}</td>
              <td>{i.subtitle}</td>
              <td>{i.url}</td>
            </tr>
          ))}
        </table>
      </div>

    )
  }
}

export default App;

